.socials{
  display: block;
  width: 60px;
  height: 60px;
  text-align: center;
  line-height: 63px;
  background: transparent;
  font-size: 40px;
  transition: .15s;
}
.socials:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: #ffee10;
  transition: .15s;
  transform: scale(.9);
  z-index: -1;
}

.socials:hover::before {
  transform: scale(1.5);
  box-shadow: 0 0 15px #ffee10;
}

.socials:hover {
  color: #ffee10;
  box-shadow: 0 0 20px #ffee10;
  text-shadow: 0 0 15px #ffee10;
}

/* For horizontal scroll */
.menu-item {
  padding: 0 10px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  border: none;
}


.scroll-menu-arrow {
  margin: 2px;
  cursor: pointer;
}

.zoom {
  padding: 10px;
  transition: transform .2s; /* Animation */
  width: 123px;
  margin: 0 auto;
}

.zoom:hover {
  transform: scale(1.41); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}
